.timeline{
    flex: 0.45;
    border-right: 1px solid var(--twitter-background);
    overflow-y: scroll;
}

.timeline::-webkit-scrollbar{
    display: none;
}

.timeline__header{
    position: sticky;
    top: 0;
    background-color:white;
    z-index: 100;
    border-bottom: 1px solid var(--twitter-background);
    padding: 5px 20px;
}

.timeline__header > h2{
    font-size: 20px;
    font-weight: 800;
}