.widgets {
    flex: 0.35;
}

.widgets__input {
    display: flex;
    align-items: center;
    background-color: var(--twitter-background);
    padding: 10px;
    border-radius: 20px;
    margin-top: 10px;
    margin-left: 20px; 
}

.widgets__input > input {
    border: none;
    outline: none;
    background-color: var(--twitter-background);
    height: auto;
    font-size: 17px;
    margin-left: 5px;
}

.widgets__searchIcon{
    color: gray !important;
}

.widgets__widgetContainer {
    margin-top: 15px;
    margin-left: 20px;
    padding: 20px;
    padding-top: 5px;
    background-color: #f5f8fa;
    border-radius: 20px;
}

.widgets__widgetContainer > h2 {
    font-size: 18px;
}